header {
    height: 110vh;
    padding-top: 4rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

#profileText{
    margin: 0 0 0 0;
}

/* ============================ CTA ====================*/

.cta {
    margin-top: 7rem;
    padding-top: 2rem;
    display: inline-flex;
    gap: 1.2rem;
    justify-content: center;
}

#resumeButton, #contactButton {
    margin: 1.5rem 0.5rem 0 0.5rem;
}

/* ============================ HEADER SOCIALS ====================*/

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 4rem;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ============================ ME ====================*/
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 24rem;
    height: 100%;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow:hidden;
    padding: 3rem 3rem 4rem 3rem;
}

/* ============================ SCROLL DOWN ====================*/

.scroll__down {
    position: absolute;
    right: 2rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ========================= MEDIA QUERIES (MEDIUM DEVICES) ======================== */

@media screen and (max-width : 1024px) {
    header { 
        height: 68vh;

    }
}

/* ========================= MEDIA QUERIES (SMALL DEVICES) ======================== */

@media screen and (max-width : 600px) {
    header { 
        height: 100vh;
    }

    .header_socials, .scroll__down {
        display: none;
    }
}